import React, { useState } from 'react';
import { Card, CardContent, Typography, Box, TextField, Button } from '@mui/material';
import styled from 'styled-components';
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { initialReviews } from './array';
import InfiniteScroll from 'react-infinite-scroll-component';
import { useSelector } from 'react-redux';
import { selectIsAutorization } from '../../redux/state/autentification/authentification.selectors';


//   
const ReviewCard = styled(Card)`
  margin-bottom: 16px;
  background: #1c1c1c;
  color: #fff;
`;

const ReviewDate = styled(Typography)`
  font-size: 0.9rem;
  color: #888;
`;

const ReviewListContainer = styled(Box)`
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
`;

const FormContainer = styled(Box)`
  margin-bottom: 20px;
`;

const LoadMoreButton = styled(Button)`
  margin-top: 20px;
  background-color: #007bff;
  color: #fff;
  &:hover {
    background-color: #0056b3;
  }
`;

//  ReviewsList
const ReviewsList = () => {
  const [items, setItems] = useState(initialReviews.slice(0, 10));
  const [hasMore, setHasMore] = useState(true);
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [reviewText, setReviewText] = useState('');

  //  
  const isAuthorized = useSelector(selectIsAutorization);

  //     
  const fetchMoreData = () => {
    if (items.length >= initialReviews.length) {
      setHasMore(false);
      return;
    }

    //      (     API)
    setTimeout(() => {
      setItems(items.concat(initialReviews.slice(items.length, items.length + 10)));
    }, 500);
  };

  //      
  const handleSubmitReview = (event) => {
    event.preventDefault();

    if (!isAuthorized) {
      toast.error(',        .');
      return;
    }

    //    
    if (name && reviewText) {
      const newReview = {
        name,
        date: new Date().toISOString().split('T')[0], //    YYYY-MM-DD
        gender: ' ', //      
        review: reviewText,
      };

      setItems([newReview, ...items]); //      
      setName('');
      setEmail(''); //  email
      setReviewText('');

      toast.success('  !'); //     
    } else {
      toast.error(',   .'); //   ,     
    }
  };

  return (
    <ReviewListContainer>
      <FormContainer>
        <form onSubmit={handleSubmitReview}>
          <TextField
            fullWidth
            margin="normal"
            label=" "
            value={name}
            onChange={(e) => setName(e.target.value)}
            required
            sx={{
              backgroundColor: '#2c2c2c',
              '& .MuiInputBase-root': {
                backgroundColor: '#2c2c2c',
              },
              '& .MuiInputLabel-root': {
                color: '#fff',
              },
              '& .MuiInputBase-input': {
                color: '#fff',
              },
              '& .MuiFormHelperText-root': {
                color: '#f5f5f5',
              },
              '& .MuiOutlinedInput-root': {
                '& fieldset': {
                  borderColor: '#444',
                },
                '&:hover fieldset': {
                  borderColor: '#007bff',
                },
                '&.Mui-focused fieldset': {
                  borderColor: '#007bff',
                },
              },
            }}
          />
          <TextField
            fullWidth
            margin="normal"
            label=" email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            sx={{
              backgroundColor: '#2c2c2c',
              '& .MuiInputBase-root': {
                backgroundColor: '#2c2c2c',
              },
              '& .MuiInputLabel-root': {
                color: '#fff',
              },
              '& .MuiInputBase-input': {
                color: '#fff',
              },
              '& .MuiFormHelperText-root': {
                color: '#f5f5f5',
              },
              '& .MuiOutlinedInput-root': {
                '& fieldset': {
                  borderColor: '#444',
                },
                '&:hover fieldset': {
                  borderColor: '#007bff',
                },
                '&.Mui-focused fieldset': {
                  borderColor: '#007bff',
                },
              },
            }}
          />
          <TextField
            fullWidth
            margin="normal"
            label=" "
            multiline
            rows={4}
            value={reviewText}
            onChange={(e) => setReviewText(e.target.value)}
            required
            sx={{
              backgroundColor: '#2c2c2c',
              '& .MuiInputBase-root': {
                backgroundColor: '#2c2c2c',
              },
              '& .MuiInputLabel-root': {
                color: '#fff',
              },
              '& .MuiInputBase-input': {
                color: '#fff',
              },
              '& .MuiFormHelperText-root': {
                color: '#f5f5f5',
              },
              '& .MuiOutlinedInput-root': {
                '& fieldset': {
                  borderColor: '#444',
                },
                '&:hover fieldset': {
                  borderColor: '#007bff',
                },
                '&.Mui-focused fieldset': {
                  borderColor: '#007bff',
                },
              },
            }}
          />
          <Button type="submit" variant="contained" color="primary">
             
          </Button>
        </form>
      </FormContainer>
      <InfiniteScroll
        dataLength={items.length} //  ,   
        next={fetchMoreData} //     
        hasMore={hasMore} // ,      
        loader={<h4>...</h4>} //   ,     
      >
        <h2>
           100 
        </h2>
        {items.map((review, index) => (
          <ReviewCard key={index}>
            <CardContent>
              <Typography variant="h6">{review.name}</Typography>
              <ReviewDate>{review.date}</ReviewDate>
              <Typography variant="body1">{review.review}</Typography>
            </CardContent>
          </ReviewCard>
        ))}
      </InfiniteScroll>
      {hasMore && (
        <LoadMoreButton onClick={fetchMoreData}>
            
        </LoadMoreButton>
      )}
      <ToastContainer />
    </ReviewListContainer>
  );
};

export default ReviewsList;
