import React, { useState } from 'react';
import { Typography, Box, Button } from '@mui/material';
import './ExchangeInstruction.css';

const ExchangeInstruction = ({ amount, fromCoin, toCoin, userWallet, calculateAmountReceived, getWalletAddress, remainingTime, email, onCancelRequest }) => {
  const [isPaid, setIsPaid] = useState(false);

  const handlePaid = () => {
    setIsPaid(true);
  };

  return (
    <Box className="exchange-instruction-container">
      <Typography variant="h6" gutterBottom>
          
      </Typography>
      <Typography variant="h6" gutterBottom>
        !   ,   .
      </Typography>
      <Typography variant="body1" paragraph>
         <strong>{amount}</strong> {fromCoin?.symbol.toUpperCase()}   :
      </Typography>
      <Typography variant="body1" paragraph>
        <strong>{getWalletAddress(fromCoin?.name)}</strong>
      </Typography>
      <Typography variant="body1" paragraph>
         : <strong>{userWallet}</strong>
      </Typography>
      <Typography variant="body1" paragraph>
             ,       .
      </Typography>
      <Typography variant="body2" color="textSecondary">
           : {Math.floor(remainingTime / 60)}  {remainingTime % 60} 
      </Typography>
      <Typography variant="body2" color="textSecondary" mt={2}>
        Email    : <strong>{email}</strong>
      </Typography>
      <Box className="button-container">
        <Button
          variant="outlined"
          color="secondary"
          onClick={onCancelRequest}
          className="cancel-button"
          disabled={isPaid} //    
        >
           
        </Button>
        <Button
          variant="contained"
          color="primary"
          onClick={handlePaid}
          disabled={isPaid}
          className="paid-button"
        >
          {isPaid ? '  ,  ' : ' '}
        </Button>
      </Box>
    </Box>
  );
};

export default ExchangeInstruction;
