import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { Formik, Form, Field } from 'formik';
import * as Yup from 'yup';

import css from './Login.module.css';
import { selectIsLoading } from '../../redux/state/autentification/authentification.selectors';
import Loader from '../../components/Loader/Loader';
import { fetchUser } from '../../redux/state/autentification/services';

//     Yup
const validationSchema = Yup.object({
  email: Yup.string()
    .email('  email')
    .required('Email '),
  password: Yup.string()
    .min(6, '    6 ')
    .required(' '),
});

const Login = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate(); //   
  const isLoading = useSelector(selectIsLoading);

  const handleSubmit = (values, { setSubmitting }) => {
    //      
    dispatch(fetchUser(values))
      .unwrap()
      .then(() => {
        setSubmitting(false);
        navigate('/exchange'); //    
      })
      .catch(() => {
        setSubmitting(false);
      });
  };

  return (
    <>
      {isLoading && <Loader />}
      <div className={css.container}>
        <h2 className={css.titleLogIn}>   </h2>
        <Formik
          initialValues={{ email: '', password: '' }}
          validationSchema={validationSchema}
          onSubmit={handleSubmit}
        >
          {({ isSubmitting }) => (
            <Form className={css.formLogIn}>
              <Field
                type="email"
                name="email"
                placeholder="Email"
                className={css.inputLogIn}
              />
              <Field
                type="password"
                name="password"
                placeholder=""
                className={css.inputLogIn}
              />
              <button
                type="submit"
                className={css.btnLogIN}
                disabled={isSubmitting}
              >
                
              </button>
            </Form>
          )}
        </Formik>
        <p className={css.profileText}></p>
      </div>
    </>
  );
};

export default Login;
